.form {
  text-align: center;
}

.form__labelText {
  display: inline-block;
  max-width: 150px;
  margin-right: 10px;
}

.form__field {
  border: 1px solid #ccc;
  padding: 3px;
  width: 200px;
  border-radius: 5px;
  width: 100px;
  margin: 18px;
}

.form__field:required {
  border-color: #777;
}

.form__field:invalid {
  background-color: hsl(0, 53%, 90%);
}

.form__button {
  width: 30%;
  border: none;
  background-color: teal;
  color: white;
  padding: 5px;
  border-radius: 5px;
}

.form__button:hover {
  background-color: hsl(180, 100%, 30%);
}

.form__button:active {
  background-color: hsl(180, 100%, 35%);
}

.form__fieldset {
  padding: 20px;
  border-radius: 5px;
  margin: 20px 0;
}

.form__legend {
  background-color: hsl(180, 100%, 25%);
  color: white;
  border-radius: 5px;
  padding: 5px;
}
